@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap);
body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
    font-family: 'Roboto Slab', serif;
    background-color: #f8f9fa;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
  display: flex;
  align-items: center;
  justify-content: center;
}

.box {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.card-login {
    flex-direction: row;
    height: 70%;
    width: 85%;
    border: none;
}

.card-login-header {
    display: flex;
    align-items: center;
    text-align: center;
    max-width: 50%;
    font-size: 52px;
    background-color: #17a2b8;
    color: white;
    font-family: 'Roboto Slab', serif;
}

.card-login-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

form {
    width: 100%;
}

.btn {
    width: 100px;
}

@media only screen and (max-width: 780px) {
    .card {
        flex-direction: column;
    }
    .card-header {
        max-width: 100% !important;
    }
  }
.rounded {
    border-radius: 1.25rem!important;
    height: 36px !important;
    margin-right: 9px;
}

.navText {
    color: white;
    margin-right: 10px;
    margin-top: 6px;
}

@media only screen and (max-width: 999px) {
   .navbar-nav {
       display: block;
   }
  }
.card-view {
    flex-direction: row;
    margin-top: 10px;
    border: none;
    border-radius: 5px;
}
.card-view-img {
    width: 25%;
    height: 26%;
    border-radius: 102px;
    margin-top: 12px;
    margin-left: 12px;
}

.btn {
    float: right;
}
.btn-warning {
    color: white;
}


.list-box {
    width: 50%;
    margin-top: 15px;
}
@media only screen and (max-width: 991px) {
    .list-box {
        width: 80%;
    }
   }
.cell {
    border-style: solid;
    padding: 11px;
    border-color: lightgray;
    border-radius: 5px;
    margin-bottom: 5px;
}

.card-view {
    flex-direction: row;
    margin-top: 10px;
}
.card-view-img {
    width: 25%;
    height: 26%;
    border-radius: 102px;
    margin-top: 12px;
    margin-left: 12px;
}

.question-view-box {
    width: 50%;
    margin-top: 15px;   
}

@media only screen and (max-width: 991px) {
    .question-view-box {
        width: 80%;
    }
   }

.leaderboard {
    width: 50%;
    margin-top: 15px;
}
@media only screen and (max-width: 991px) {
    .leaderboard {
        width: 80%;
    }
   }
